

export function applyAttributes() {

}

function applyInlineStyle(element: HTMLElement, style: HTML.CSS) {
    let elemStyle = element.style;
    let name: keyof HTML.CSS;
    for (name in style) {
        elemStyle[name] = style[name];
    }
}

/**
 * Global class for DOM manipulation
 */
class DOMManipulator {

    private _body: HTMLBodyElement;

    public get body(): HTMLBodyElement {
        return this._body;
    }

    constructor() {
        this._body = document.getElementsByTagName("BODY")[0] as HTMLBodyElement;
    }

    createElement<K extends HTMLTagNames>(tagName: K, attr?: HTML.AttributesTagMap[K]): HTMLElementTagNameMap[K] {
        let node = document.createElement(tagName);

        console.log(attr)

        if (attr !== undefined || attr !== null) {
            for (const iterator in attr) {
                const value = attr[iterator];

                if (typeof (value) !== 'string') {

                    if (iterator === 'style') {
                        if (value) {
                            applyInlineStyle(node, value);
                        }
                    } else {

                        node.setAttribute(iterator, value.toString());
                    }
                } else {
                    node.setAttribute(iterator, value);
                }


            }
        }

        return node;
    }
}

const DOM = new DOMManipulator();

export default DOM;
